.custom-table{border-collapse:collapse}@media(max-width: 576px){.custom-table{width:100%}}.custom-table th{position:relative;color:#fff;background:#2e77ff;font-weight:600}.custom-table th:nth-child(2n){background:#4e8cff}.custom-table th.sorted-asc::after{position:absolute;right:8px;top:50%;transform:translate(0%, -50%);font-size:10px;content:"▲"}.custom-table th.sorted-desc::after{position:absolute;right:8px;top:50%;transform:translate(0%, -50%);font-size:10px;content:"▼"}.custom-table th button{width:100%;padding:10px 20px;border:none;background:inherit}.custom-table td{max-width:200px;padding:15px;overflow:hidden;text-overflow:ellipsis}.custom-table th,.custom-table td{max-width:200px;text-align:center;vertical-align:middle;overflow:hidden;text-overflow:ellipsis}.custom-table--filled tr{position:relative;transition:background .3s}.custom-table--filled tr:hover{background:#ebebeb}.custom-table--filled tr:hover td:last-child{display:block}.custom-table--filled td:not(:last-child){border-right:1px solid #ccc}.custom-table--filled td:last-child{display:none;position:absolute;top:50%;right:5px;transform:translate(0%, -50%);padding:0;transition:opacity 1s}.overflow-element{overflow-x:auto;box-shadow:0px 2px 10px 2px rgba(0,0,0,.3)}@media(max-width: 576px){.overflow-element{width:100%}}